@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom classes to mimic Figma look */
.sidebar-gradient {
    background: linear-gradient(180deg, #1b0f4a 0%, #2f0f6a 50%, #42107f 100%);
}

.panel {
    background-color: #f8f5ff; /* very pale lilac */
    border-radius: 18px;
    box-shadow: 0 6px 18px rgba(33,24,58,0.06);
}

/* Card used for sensor values */
.sensor-card {
    background: linear-gradient(180deg, #fff 0%, #fbf8ff 100%);
    border-radius: 14px;
    padding: 18px;
    box-shadow: 0 8px 30px rgba(33,24,58,0.04);
    border: 1px solid rgba(20,12,48,0.03);
}

/* Accent small dark card used for switch */
.control-card {
    background: linear-gradient(180deg, #2b0e6a, #2f1773);
    color: white;
    border-radius: 16px;
    padding: 14px;
    width: 160px;
}

/* Toggle switch */
.toggle {
    --w: 44px;
    --h: 24px;
    width: var(--w);
    height: var(--h);
    border-radius: 999px;
    background: rgba(255,255,255,0.22);
    position: relative;
    display: inline-block;
}
.toggle .dot {
    width: 18px;
    height: 18px;
    background: white;
    border-radius: 50%;
    position: absolute;
    top: 3px;
    left: 4px;
    transition: all 0.18s ease;
    box-shadow: 0 3px 8px rgba(16,12,40,0.25);
}
.toggle.on {
    background: linear-gradient(90deg,#00d4a3,#00a3ff);
}
.toggle.on .dot {
    left: calc(var(--w) - 22px);
}

/* Table styling */
.data-table th {
    background: transparent;
    color: #9aa4b2;
    font-weight: 600;
}
.data-table td {
    padding: .75rem 1rem;
    color: #21303f;
}
